/*--------animation---------*/

@keyframes fadeInOut {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.3;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeHeaderInDown {
  0% {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
@keyframes marquee {
  0% {
    margin-left: 0;
  }
  100% {
    margin-left: calc(-100%);
  }
}
@keyframes ripple {
  0% {
    transform: scale(1);
  }
  75% {
    transform: scale(1.75);
    opacity: 1;
  }
  100% {
    transform: scale(2);
    opacity: 0;
  }
}
@keyframes shadow {
  0% {
    transform: scale(1) translateY(0px);
  }
  100% {
    transform: scale(0.8) translateY(75px);
  }
}
@keyframes ghost {
  0% {
    transform: scale(1) translateY(0px);
  }
  100% {
    transform: scale(1) translateY(-40px);
  }
}
@keyframes movetotop {
  50% {
    bottom: 220px;
  }
}
@keyframes movetobottom {
  50% {
    top: 220px;
  }
}
@keyframes box_ripple {
  0% {
    transform: scale(0, 0);
    opacity: 0.3;
  }
  40% {
    transform: scale(15, 15);
    opacity: 0.3;
  }
  100% {
    opacity: 0;
    transform: scale(40, 40);
  }
}
