/*side-lines*/
.side-lines {
  position: absolute;
  top: 80px;
  left: 5%;
  z-index: 1;
  height: calc(100% - 160px);
}
.side-lines.right-side {
  right: calc(5% - 30px);
  left: auto;
}
.side-lines .box {
  display: block;
  width: 30px;
  height: 30px;
  background: #27346a;
  margin-left: -13.5px;
  z-index: 1;
  position: relative;
  cursor: pointer;
}
.side-lines .box:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 5px;
  height: 5px;
  background: rgba(0, 112, 186, 0.3);
  opacity: 0;
  transform: scale(1, 1) translate(-50%);
  transform-origin: 50% 50%;
  z-index: 1;
}
.side-lines .box:hover:before {
  animation: box_ripple 1s cubic-bezier(0.18, 0.74, 0.72, 1);
  text-decoration: none;
}
.side-lines .text {
  transform: rotate(-90deg);
  background: #fff;
  height: 32px;
  text-align: center;
  display: flex;
  align-items: center;
  padding: 0 30px;
  font-size: 22px;
  color: #081f4f;
  margin-left: -64px;
  margin-top: 48px;
}
.side-lines .line {
  display: block;
  background: #081f4f;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  width: 3px;
  z-index: -1;
}
.side-lines.style-2 .box {
  background: #081f4f;
}
.side-lines.style-2 .text {
  background: transparent;
  color: #fff;
}
.side-lines.style-2 .line {
  background: #fff;
  top: 160px;
  height: calc(100% - 160px);
}
