}
.extra-page .corner.three {
  margin-left: 66px;
}
.extra-page .corner.four {
  margin-left: 98px;
}
.extra-page .over {
  width: 20px;
  height: 20px;
  border-radius: 3px;
  background-color: #081f4f;
  position: absolute;
  margin-top: 185px;
  transform: rotate(45deg) translateX(3px) translateY(2px);
}
.extra-page .over.two {
  margin-left: 33px;
}
.extra-page .over.three {
  margin-left: 66px;
}
.extra-page .over.four {
  margin-left: 98px;
}
